<template>
        <div class="main__map">
            <template v-if="coords">
            <div class="points" :style="item" v-for="item in getCoordinates" :key="item">
                <div class="point__info">{{coords.itemInfo}}</div>
            </div>
            <div class="ekb">
                Екатеринбург
            </div>
            <div class="tagil">
                Нижний Тагил
            </div>
            </template>
            <img class="map__image" src="../../assets/map.png" alt="">
            </div>
</template>

<script>

export default {
    props: ['coordsToMap'],
    data(){
        return {
            coords: null,
       
        }
    },
    computed: {
        getCoordinates(){

            return JSON.parse(this.coords.itemCoord);
        }
    },

    watch: {
        coordsToMap(){
            if(this.coordsToMap !== {}){
                this.coords = this.coordsToMap;

            }
        }
    }
}
</script>

<style lang="scss">
   .main__map{
          border: 3px solid #FFEFD5;
        // position: relative;
        // width: 40%;
        height: 100%;
    }
    .map__image{
        height: 100%;
        width: 100%;
        display: block;
    }
    .points{
        cursor: pointer;
        transform-origin: center;
        z-index: 11;
        position: absolute;
        content: '';
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background-color: red;
        transition: transform .5s;
    }
    .point__info{
        box-shadow: -16px 14px 6px 7px rgba(34, 60, 80, 0.5);
        display: none;
        position: absolute;
        transform: translateX(50px);
        background-color: aqua;
        text-align: center;
        z-index: 110;
    }
    .points:hover > .point__info{
        display: block;
    }
    .points:hover{
        transform: scale(1.5);
    }
    @media(max-width: 1024px){
        .points{
            width: 15px;
            height: 15px;
        }
    }
    .ekb{

        transform-origin: center;
        z-index: 10;
        position: absolute;
        left: 35%;
        bottom: 12%;
        content: '';
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: blue;
        transition: transform .5s;
    }
        .tagil{

        transform-origin: center;
        z-index: 10;
        position: absolute;
        left: 28%;
        bottom: 28%;
        content: '';
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: blue;
        transition: transform .5s;
    }
</style>
