
<template>
<div class="main__background">
    <div class="main__wrapper">
        <div class="book__main">
  <div class="book__mainest">
            <div class="book__wrapper" :class="{active: wrapperFlag}">
    
        <aside-component @coordsToMain="coordHoisting">
        </aside-component>
            </div>
            <div class="book__second" >
                     <div class="book__seca" :class="{active: wrapperFlag}" @click="wrapper()"><h1 class="book__title" v-if="!wrapperFlag">Красная книга Свердловской области</h1></div>
        <map-component :coordsToMap="objectCoords">
        </map-component>
        
        </div>

     
</div>
    </div>
    </div>
</div>
</template>

<script>
import asideComponent from './aside-component.vue';
import mapComponent from './map-component.vue';

export default {
  components: { asideComponent, mapComponent },
  name: "MainPage",
  data(){
      return {
        objectCoords: {},
        wrappist: false,
        wrapperFlag: false,
      }

  },
  methods: {
      coordHoisting(value){
          this.objectCoords = value;

      },
      wrapper(){
     this.wrapperFlag = !this.wrapperFlag;
     setTimeout(() => {this.wrappist = !this.wrappist}, 2000)
      },


  },
  watch: {
      objectCoords(){
          return this.objectCoords;
      }
  }

}
</script>

<style lang="scss">
    .main__background{
